.history__item {
  position: relative;
  margin-bottom: 30px;
  padding-left: 35px;
  font-size: 20px;
  line-height: 28px; }
  @media screen and (max-width: 767px) {
    .history__item {
      font-size: 16px;
      line-height: 24px; } }
  .history__item:before {
    content: "";
    display: block;
    position: absolute;
    width: 10px;
    height: 10px;
    left: 0;
    top: 10px;
    background-color: #93191E; }

.audience__title {
  margin-bottom: 40px; }

.audience__flex {
  display: flex;
  justify-content: space-between;
  margin-bottom: 90px; }
  @media screen and (max-width: 1365px) {
    .audience__flex {
      flex-direction: column; } }
  @media screen and (max-width: 767px) {
    .audience__flex {
      margin-bottom: 30px; } }

.audience__el:first-child {
  flex-shrink: 0;
  width: 100%;
  max-width: 740px;
  margin-right: 20px; }
  @media screen and (max-width: 1365px) {
    .audience__el:first-child {
      margin-right: 0;
      margin-bottom: 30px;
      max-width: 100%; } }

.audience__el:last-child {
  display: flex;
  justify-content: center; }

.audience__description {
  font-size: 20px;
  line-height: 32px; }
  @media screen and (max-width: 767px) {
    .audience__description {
      font-size: 16px;
      line-height: 24px; } }

.audience__rule {
  font-size: 20px;
  line-height: 32px;
  color: #000000; }
  @media screen and (max-width: 767px) {
    .audience__rule {
      font-size: 16px;
      line-height: 24px; } }
  .audience__rule:nth-child(2) {
    margin-bottom: 40px; }
  .audience__rule:nth-child(3) {
    margin-bottom: 120px; }
    @media screen and (max-width: 767px) {
      .audience__rule:nth-child(3) {
        margin-bottom: 50px; } }

.audience__link {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 20px;
  line-height: 32px;
  color: #E10C0C;
  transition: 0.5s; }
  .audience__link:hover {
    opacity: 0.6; }

.indexing {
  border-bottom: 2px solid #93191E; }

.indexing__flex {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 70px; }
  @media screen and (max-width: 479px) {
    .indexing__flex {
      flex-direction: column;
      align-items: start; } }

.indexing__img {
  margin-right: 60px;
  margin-bottom: 40px; }
  @media screen and (max-width: 767px) {
    .indexing__img {
      height: 15px;
      width: auto; } }

.footer {
  padding-top: 80px; }

.footer__text {
  margin-bottom: 40px;
  font-size: 16px;
  line-height: 24px; }

.footer__certificate {
  margin-bottom: 60px;
  font-size: 12px;
  line-height: 18px; }
